@import url(./font.css);

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  color: white;
  text-align: center;
  background-color: black;
  background-image: url(../selection.jpg);
  background-size: cover;
}
header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  /* border-bottom: solid white 1px;*/
}
canvas {
  /* display: none !important;*/
}
header .navbar {
  height: 120px;
}

main.intro {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 0 10vw;
  z-index: 100;

  /*display: none;*/
}
main.intro .content-conteiner {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 600px;
  text-align: left;
}
h1,
h4,
p {
  margin: 0;
}
/*animations props*/
.text-animated-conteiner {
  overflow: hidden;
  /*  border:  solid red 1px;*/
  width: 550px;
}
.animated-item {
  position: relative;
}
div.content-conteiner .sub-title {
  font-family: Monzerrat-regular;
  color: rgb(191, 191, 191);
  font-size: 14px;
  margin-left: 5px;
}
div.content-conteiner .title {
  font-family: Monzerrat-medium;
  font-size: 60px;
  line-height: 1;
  margin-top: 10px;
}
div.content-conteiner .title.span {
  font-size: 45px;
  font-family: Monzerrat-regular;
}
