div.content-conteiner .description {
  font-family: Monzerrat-regular;
  line-height: 1.4;
  margin-top: 10px;
  margin-left: 5px;
  color: rgb(191, 191, 191);
}
div.content-conteiner .start-ui {
  margin-top: 20px;
  margin-bottom: 100px;
  padding: 15px 0;
  color: whitesmoke;
}


div.start-ui {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 4px;
}
div.start-ui button {
  background-color: unset;
  border-style: unset;
  outline: 2px whitesmoke solid;
  transition: box-shadow 0.8s;
  color: white;

}
div.ui-controls button img{
  filter:invert();
  width: 20px;
  height:20px;
}
div.start-ui button:hover {
  transition: box-shadow 0.8s;
  -webkit-box-shadow: inset 0px 0px 15px 5px #15EDED, 0px 0px 10px 2px #15EDED;
  -moz-box-shadow: inset 0px 0px 15px 5px #15EDED, 0px 0px 1px 2px #15EDED;
  box-shadow: inset 0px 0px 15px 5px #15EDED, 0px 0px 10px 2px #15EDED;
  
}
div.start-ui button.ui-title {
  font-family: Monzerrat-regular;
  font-size: 12px;
  margin-left: 8px;
  padding: 10px 20px;
  border-radius: 35px;
}
div.start-ui div.ui-controls {
  position: fixed;
  height: 180px;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
div.start-ui button.control {
  margin-right: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

div.ui-controls button#start-scene {
  margin-right: calc(50% - 170px);
  display: grid;
  place-content: center;
}
div.ui-controls button#start-scene img{
  position: relative;
  transition: transform 0.5s;
}
div.ui-controls button#start-scene :hover{
  transition: transform 0.5s;
  transform: scale(1.3);
}

div.ui-controls button#start-scene::after {
  text-align: center;
  position: absolute;
  margin-left: -50px;
  margin-top: -25px;
  width: 150px;
  display: block;
  content: "CLICK TO START";
}

div.ui-controls button#toggle-sound {
  margin-right: 100px;
}


.dc.ac {
  z-index: 100 !important;
}
